$bg-light: #FAFAFA;
$blue: #23A6F0;
$grey: #737373;
$dark: #252B42;
$white: #FFFFFF;
$light-gray: #DEDEDE;
$card-border: #E9E9E9;

.title {
    font-weight: 700;
    font-size: 40px;
    line-height: 57px;
    text-align: center;
    letter-spacing: 0.2px;
    color: $dark;
    margin: 0 auto; }
.description {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.2px;
    color: $grey;
    margin: 0 auto; }
.card {
    border: 1px solid $light-gray;
    background-color: $white; }
